<template>
  <div id="main">
  <div id="wrap">
    <div id="login">
      <img id="loginpng" src="../assets/forgot_password.png" alt="" />
    </div>
    <div id="loginwrap">
      <h1>重置密码</h1>
      <table id="email">
        <tr>
          <td>邮箱</td>
        </tr>
        <tr>
          <input id="emailinput" type="text" />
        </tr>
      </table>
      <button id="register">发送</button>
      <span id="had">
        您已有账号？
        <a id="enter" href="#">用户登录</a>
      </span>
      <span class="forget">
        建立新账号？
        <a href="#" id="userRegister">用户注册</a>
        </span>
    </div>
  </div>
  </div>
</template>
<script>
export default {
  name: 'Forget',
}
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
#main{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#wrap {
  width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
#login {
  position: relative;
  float: left;
  width: 720px;
  height: 1024px;
  background-color: #9a9aeb;
}
#loginpng {
  position: absolute;
  top: 271px;
  right: 0px;
}
#loginwrap {
  position: relative;
  float: left;
  width: 720px;
  height: 1024px;
  background-color: #f7f7f7;
}
h1 {
  position: absolute;
  top: 297px;
  left: 286px;
  font-size: 32px;
}
#email {
  position: absolute;
  top: 378px;
  left: 127px;
  width: 446px;
  height: 103px;
  font-size: 24px;
}
#emailinput {
  width: 446px;
  height: 55px;
  font-size: 30px;
  text-indent: 10px;
}

#register {
  position: absolute;
  top: 525px;
  left: 127px;
  width: 446px;
  height: 55px;
  font-size: 24px;
  background-color: #000000;
  color: #ffffff;
}
#had {
  position: absolute;
  top: 623px;
  left: 127px;
  display: block;
  font-size: 20px;
}
#enter{
  text-decoration: none;
  color: #4D4DDB;
}
#userRegister{
  text-decoration: none;
  color: #4D4DDB;
}
.forget {
  position: absolute;
  top: 623px;
  left: 359px;
  font-size: 20px;
}
button{
  cursor: pointer;
}
</style>


