<template>
 <div id="main">
  <div id="wrap">
    <div id="login">
      <img id="loginpng" src="../assets/undraw_my_password.png" alt="" />
    </div>
    <div id="loginwrap">
      <h1>重置密码</h1>
      <table id="password">
        <tr>
          <td>密码</td>
        </tr>
        <tr>
          <input id="passwordinput" type="password" />
          <img id="passwordbg1" src="../assets/Group.png" alt="" />
        </tr>
      </table>
      <table id="repeatpassword">
        <tr>
          <td>重复密码</td>
        </tr>
        <tr>
          <input id="passwordinput" type="password" />
          <img id="passwordbg1" src="../assets/Group.png" alt="" />
        </tr>
      </table>
      <p id="passwordstrength">密码强度</p>
      <span id="strength">很强</span>
      <button id="register">注册</button>
      <span id="had">
        没有账号？
        <a id="userRegister" href="#">用户注册</a>
      </span>
      <span id="userlogin">
        已有账号
        <a href="#" id="enter">用户登录</a>
      </span>
    </div>
  </div>
 </div>
</template>
<script>
export default {
  name: "Resetpassword",
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
#main{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#wrap {
  width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
#login {
  position: relative;
  float: left;
  width: 720px;
  height: 1024px;
  background-color: #9a9aeb;
}
#loginpng {
  position: absolute;
  top: 301px;
  left: 96px;
}
#loginwrap {
  position: relative;
  float: left;
  width: 720px;
  height: 1024px;
  background-color: #f7f7f7;
}
h1 {
  position: absolute;
  top: 225px;
  left: 286px;
  text-align: center;
  font-size: 32px;
}
#password {
  position: absolute;
  top: 301px;
  left: 127px;
  width: 446px;
  height: 103px;
  font-size: 24px;
}
#passwordinput {
  width: 446px;
  height: 55px;
  font-size: 30px;
  text-indent: 10px;
}
#repeatpassword {
  position: absolute;
  top: 424px;
  left: 127px;
  width: 446px;
  height: 103px;
  font-size: 24px;
}
#repeatpasswordinput {
  width: 446px;
  height: 55px;
  font-size: 30px;
  text-indent: 10px;
}
#passwordbg1 {
  position: absolute;
  top: 60px;
  left: 400px;
  cursor: pointer;
}

#passwordstrength {
  position: absolute;
  top: 551px;
  left: 127px;
  font-size: 20px;
}
#strength{
  position: absolute;
   top: 551px;
   left: 220px;
   font-size: 20px;
  color: #00FF85;
}
#register {
  position: absolute;
  top: 598px;
  left: 127px;
  width: 446px;
  height: 55px;
  font-size: 24px;
  background-color: #000000;
  color: #ffffff;
}
#had {
  position: absolute;
  top: 700px;
  left: 127px;
  display: block;
  font-size: 20px;
}
#userRegister {
  text-decoration: none;
  color: #4d4ddb;
}
#enter{
  text-decoration: none;
  color: #4D4DDB;
}
#userlogin {
  position: absolute;
  top: 700px;
  left: 405px;
  font-size: 20px;
}
button{
  cursor: pointer;
}
</style>


